.opennav {
  width: 250px !important;
}
.closenav {
  width: 70px !important;
}

.sidenav {
  height: 100%;
  width: 0;
  position: fixed;
  z-index: 1;
  top: 0;
  left: 0;
  bottom: 0;
  background: #466368;
  background: linear-gradient(to right bottom, #648880, #293f50);
  overflow-x: hidden;
  transition: 0.5s;
  padding-top: 60px;

  border-right: 1px solid #d8e3e7;
}

.lefnav-list li {
  list-style: none;
  font-size: 16px;
  font-weight: 500;
  padding: 10px 5px;
  position: relative;
  white-space: nowrap;
  text-align: left;
}
ul.lefnav-list li a {
  color: #fff;
}
.navbar-header {
  position: relative;
  bottom: 65px;
  border-bottom: 1px solid #d8e3e7;
}

.leftnav-gly {
  right: 24px;
  font-size: 22px;
}

.sidenav::-webkit-scrollbar {
  width: 6px;
}

.sidenav::-webkit-scrollbar-track {
  -webkit-box-shadow: inset 0 0 6px rgba(0, 0, 0, 0.3);
  border-radius: 10px;
}

svg rect {
    fill: orange;
}

svg text {
    fill:white;
    font: 10px sans-serif;
    text-anchor: end;
}


